import React from 'react'

import './postPage.css'

import CommentBlock from './CommentBlock/CommentBlock'
import {getPostObject} from '../BlogPage/BlogPosts/blogPosts'
import {connect} from 'react-redux'

const PostPage = ({
    posts,
    match,
    postObject = getPostObject(posts),
    isLiked,
    addLike,
    removeLike,
}) => {

    const id = match.params.id

    console.log(postObject[id].title)

    if(posts.length===0) {
        return null;
    } else {

        return (
            <>
            <div className="sticky-picture">
                <img src={postObject[id].imageBackground} alt=""/>
            </div> 
                <main className="main">
                    <div className="container">
                        <div className="post-page-row">
                            <div className="post">
                                <div className="title-like-box">
                                    <div className="post-article-title">{postObject[id].title}</div>
                                    <div 
                                        onClick={() => isLiked ? removeLike(id) : addLike(id)}
                                        className="post-like">
                                        {isLiked ? <div className="post-like liked"></div> : <div className="post-like"></div>}                        
                                    </div>
                                </div>
                                <div className="post-article-photo">
                                    <img src={postObject[id].image} alt=""/>
                                    <div className="post-article-info">
                                        <div className="post-date">{postObject[id].date}</div>
                                        <div className="post-category">{postObject[id].category}</div>
                                        <div className="post-author">by {postObject[id].author}</div>
                                    </div>
                                </div>
                                <div className="post-full-text"
                                    dangerouslySetInnerHTML={{
                                        __html:postObject[id].fulltextPartOne
                                    }}>
                                </div> 
                                <div className="pictures-block">
                                    <img src={postObject[id].image1} alt=""/>
                                    <img src={postObject[id].image2} alt=""/>
                                    <img src={postObject[id].image3} alt=""/>
                                    <img src={postObject[id].image4} alt=""/>
                                    <img src={postObject[id].image5} alt=""/>
                                    <img src={postObject[id].image6} alt=""/>
                                    <img src={postObject[id].image7} alt=""/>
                                    <img src={postObject[id].image8} alt=""/>
                                    <img src={postObject[id].image9} alt=""/>
                                    <img src={postObject[id].image10} alt=""/>
                                </div>
                                <div className="post-full-text"
                                    dangerouslySetInnerHTML={{
                                        __html:postObject[id].fulltextPartTwo
                                    }}>
                                </div>
                            </div>    
                            <div className="posts-sidebar">
                                <CommentBlock
                                    postTitle={postObject[id].title}
                                />
                            </div>
                        </div>
                    </div>
                </main>
            </>
        )
    }
}

const mapState = (state,{match}) => ({
    isLiked:state.postLikeState[match.params.id]
})

const mapDispatch = dispach => ({
    addLike:(id) => dispach ({
        type:"LIKE",
        id
    }),
    removeLike:(id) => dispach ({
        type:"DISLIKE",
        id
    })
})

export default connect(
    mapState,
    mapDispatch

)(PostPage)
