export const postReducer = (state = {
    posts:[],
    loading:true
}, action) => {
    switch(action.type){
        case "GET_POSTS":
        return {
            ...state,
            posts:action.payload,
            loading:false
        }
        case "POST_ERROR":
            return{
                loading: false, 
                error: action.payload 
            }
        default: return state
    }

}