import {omit} from 'lodash'

export const postLikeReducer = (state = {
    1:true,
    4:true,
    6:true
    
},action) => {
    switch (action.type) {
        case "LIKE":
            return {
                ...state,
                [action.id]:true,
            }
        case "DISLIKE":
            return omit(state, action.id)

        default:
            return state;
    }
}
